<template>
  <a-table :columns="columns" @change="changePage" :data-source="data" :pagination="page">
    <div slot="options" slot-scope="record">
      <a-button @click="editPro(record)"></a-button>
      <a-button @click="removePro(record)"></a-button>
    </div>
  </a-table>
</template>
<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    width: 80,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'title',
    key: 'title',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'desc',
    key: 'desc',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'categoryName',
    key: 'category',
    width: 100,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'price',
    key: 'price',
    width: 90,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'price_off',
    key: 'price_off',
    width: 90,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'tags',
    key: 'tags',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'inventory',
    key: 'inventory',
    width: 80,
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'status',
    key: 'status',
    width: 90,
    align: 'center',
    customRender(text) {
      return text === 1 ? '' : '';
    },
  },
  {
    title: '',
    key: 'options',
    scopedSlots: { customRender: 'options' },
    align: 'center',
  },
];

export default {
  props: {
    data: Array,
    total: Number,
  },
  data() {
    return {
      columns,
      page: {
        showSizeChanger: true,
        total: this.total,
        current: 1,
        pageSize: 10,
      },
    };
  },
  watch: {
    total() {
      this.page.total = this.total;
    },
  },
  methods: {
    changePage(page) {
      this.page = page;
    },
    editPro(record) {
      // console.log(record);
      this.$emit('editProduct', record);
    },
    removePro(record) {
      this.$confirm({
        title: '',
        content: () => <div style="color:red;">{`:  ${record.title}  `}</div>,
        okText: '',
        okType: 'danger',
        cancelText: '',
        onOk: () => {
          this.$emit('removeProduct', record);
        },
        onCancel: () => {
          console.log('');
        },
      });
    },
  },
};
</script>
