<template>
  <a-table
    :columns="columns"
    :data-source="data"
    :pagination="page"
    @change="changePage"
  >
    <div slot="options" slot-scope="record">
      <a-button @click="editPro(record)"></a-button>
      <a-popconfirm
        title=""
        ok-text=""
        cancel-text=""
        @confirm="confirm(record)"
      >
        <a-button></a-button>
      </a-popconfirm>
    </div>
  </a-table>
</template>
<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    key: 'options',
    scopedSlots: { customRender: 'options' },
  },
];

export default {
  props: {
    data: Array,
  },
  data() {
    return {
      columns,
      page: {
        showSizeChanger: true,
        total: 10,
        current: 1,
        pageSize: 10,
      },
    };
  },
  methods: {
    editPro(record) {
      this.$emit('edit', record);
    },
    confirm(record) {
      this.$emit('remove', record);
    },
    changePage(page) {
      this.page = page;
    },
  },
};
</script>
